<template>
  <div class="pwd-login-container">
    <div class="login-tab clearfix">
      <router-link tag="div" to="/personCenter/smsLogin" class="login-tab-item">
        <span  class="login-link">短信登录</span>
      </router-link>
      <router-link tag="div" to="/personCenter/pwdLogin" class="login-tab-item">
        <span  class="login-link">密码登录</span>
      </router-link>
    </div>
    <form action="" @submit.prevent="submit" class="comForm">
      <div class="input-wrapper">
        <input type="text" v-model="pwdLoginForm.idNum"  @focus="focus($event)" @blur="blur($event)" placeholder="身份证号">
        <i class="del" v-show="pwdLoginForm.idNum" @mousedown.stop="del('idNum')" ></i>
      </div>
      <div class="input-wrapper msgcode-wrapper clearfix">
        <input type="text" v-model="pwdLoginForm.pwd"  @focus="focus($event)" @blur="blur($event)" placeholder="密码">
        <i class="del" v-show="pwdLoginForm.pwd" @mousedown.stop="del('pwd')"></i>
      </div>
      <div class="submit-wrapper">
        <div class="error-tips" v-show="error">{{error}}</div>
        <input type="submit" value="登录" class="submit" :class="allValid? 'active':''" :disabled="!allValid">
      </div>
      <div class="clearfix forget-register-wrapper">
        <router-link tag="div" to="/c" class="forget-btn">
          <span  class="forget-link">忘记密码</span>
        </router-link>
        <router-link tag="div" to="/personCenter/register" class="register-btn">
          <span  class="register-link">立即注册</span>
        </router-link>
      </div>
    </form>
  </div>
</template>

<script>
// import { ValidateUtil } from 'common/js/global'
import api from '@/api'
import { getComForm } from '@/assets/js/mixins/comForm'
import { ValidateUtil } from '@/assets/js/global'
// import { uuid } from '@/assets/js/util'
import { mapActions } from 'vuex'

export default {
  name: 'pwdLogin',
  mixins: [getComForm],
  data () {
    return {
      error: '',
      isMessageCode: false,
      pwdLoginForm: {
        idNum: '',
        pwd: ''
      }
    }
  },
  mounted () {
    this.getList()
    // var cc = uuid()
    console.log('ff')
  },
  methods: {
    getList () {
      api.getProductList().then(res => {
        console.log(12, res)
      })
    },
    submit () {
      // else if (!ValidateUtil.idCardRegexs(this.loginForm.idNum)) {
      //     this.error = '身份证格式错误'
      //   }
      if (ValidateUtil.idCardRegexs(this.pwdLoginForm.idNum)) {
        this.idIdNumValid = true
      } else {
        this.error = '身份证格式错误'
      }
      if (!this.loginForm.idNum || !this.loginForm.msgCode) {
        this.error = '输入不得为空'
      } else {
        // this.$store.dispatch('Logins', this.loginForm)
        this.Logins(this.loginForm)
      }
    },
    del (type) {
      this.pwdLoginForm[type] = ''
    },

    ...mapActions([
      'Logins'
    ])
  },
  computed: {
    allValid () {
      return this.pwdLoginForm.idNum && this.pwdLoginForm.pwd
    }
  },
  components: {}

}
</script>
<style scoped lang="scss">
  @import "@/assets/styles/comForm.scss";
  .pwd-login-container{
    .login-tab{
      height: 41px;
      padding-top:50px;
      .login-tab-item{
        float: left;
        padding-right:50px;
        font-size: 20px;
        color: #333333;
        letter-spacing: 0;
        &.router-link-active{
          .login-link{
            padding-bottom:15px;
            font-weight: bold;
            color: #333333;
            border-bottom: 3px solid #1C1D1D;
            box-sizing: border-box;
          }
        }
      }

    }
  }

</style>
